import React, { useState, useEffect, useRef } from "react";
import { useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import styled from "styled-components";
import axios from "axios";
import ArrowDropUpIcon from "@mui/icons-material/ArrowDropUp";
import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import SellStone from "../../components/Stones/SellStone";
import TradeStoneModal from "../../components/Stones/TradeStoneModal";
import PreviewStream from "../../components/PreviewStream";

export function TradeStone() {
  const modalRef = useRef();

  const { id } = useParams();
  const [klayPrice, setKlayPrice] = useState(0);
  const [stoneData, setStoneData] = useState({
    stoneDetail: null,
    musician: null,
    sellList: [],
    minPrice: null
  });
  const [modalOpen, setModalOpen] = useState(false);
  const [modalTrade, setModalTrade] = useState("");
  const account = useSelector((state) => state.accountReducer);
  const server =
    process.env.REACT_APP_SERVER_ADDRESS || "http://127.0.0.1:12367";

  const showName = () => {
    const kName = stoneData.musician.name_korea;
    const eName = stoneData.musician.name_english;

    if (kName && eName) {
      return `${kName}(${eName})`;
    } else if (kName) {
      return kName;
    } else {
      return eName;
    }
  }

  const showDesc = () => {
    const desc = stoneData.stoneDetail.description.split('\n');
    return (<Desc>
      {desc.map((line) => {
        line = line === '\r' ? ' ' : line;
        return <div>{line}</div>
      })}
    </Desc>)
  }

  const showSellList = () => {

    return stoneData.sellList.map((trade) => {
      return (
        <SellStone
          trade={trade}
          klayPrice={klayPrice}
          handleBuyBtn={handleBuyBtn}
        />
      );
    });
  };
  /*     
const showPriceDif = () => {
  const priceDif = (StoneData.minPrice - StoneData.prevPrice).toFixed(2);
  const difPercent = (
    (StoneData.minPrice / StoneData.prevPrice - 1) *
    100
  ).toFixed(2);
  return (
    <PriceDif color={priceDif > 0 ? "#e81a46" : "#00a1ff"}>
      {priceDif > 0 ? <ArrowDropUpIcon /> : <ArrowDropDownIcon />}
      {`${priceDif} (${difPercent}%)`}
    </PriceDif>
  );
};*/


  const addPlayList = () => {
    if (account.isConnect) {
      const req = `${server}/playlist/${account.userId}`;
      axios
        .post(req, {
          stoneId: stoneData.stoneDetail.id,
        })
        .then((res) => {
          if (res.status === 201) {
            alert(" .");
          } else if (res.status === 200) {
            alert("  .");
          } else {
            alert("  ");
          }
        });
    } else {
      alert(" .");
    }
  };

  const handleBuyBtn = (trade) => {
    modalRef.current.resetQuantity();
    setModalTrade(trade);
    setModalOpen(true);
  };


  useEffect(() => {
    //KLAY  
    const apiUrl = `https://api.bithumb.com/public/ticker/KLAY_KRW`;
    axios
      .get(apiUrl)
      .then((price) => setKlayPrice(price.data.data.closing_price));

    const req = `${server}/stones/tradestone/${id}`
    axios.get(req)
      .then((res) => setStoneData(res.data))
  }, []);

  return (
    stoneData.stoneDetail
      ?
      <Body>
        <StoneContainer>
          <PreviewStream img={stoneData.albumImg} stoneId={stoneData.stoneDetail.id} />
          <StoneBox>
            <StoneWrapper>
              <Title>
                <span>
                  {stoneData.stoneDetail.name} - {stoneData.musician ? showName() : 'unknown'}
                </span>
                <AddPlaylistBtn onClick={addPlayList}>+Playlist</AddPlaylistBtn>
              </Title>
              <Info>
                <Price>
                  <div> : {stoneData.minPrice} KLAY</div>
                  {/*<div> : {showPriceDif()}</div>*/}
                </Price>
                <Lyricist> : {stoneData.stoneDetail.lyricist}</Lyricist>
                <Composer> : {stoneData.stoneDetail.composer}</Composer>
                {showDesc()}
              </Info>
            </StoneWrapper>
          </StoneBox>
        </StoneContainer>
        <TradeContainer>
          <SellListWrapper>
            <SellNav>
              <span>(Klay)</span>
              <span>()</span>
              <span></span>
              <span></span>
              <span></span>
            </SellNav>
            <SellList>{showSellList()}</SellList>
          </SellListWrapper>
          <NotifyWrapper>{showRules()}</NotifyWrapper>
        </TradeContainer>
        <TradeStoneModal
          ref={modalRef}
          klayPrice={klayPrice}
          stoneData={stoneData}
          modalOpen={modalOpen}
          modalTrade={modalTrade}
          setModalOpen={setModalOpen}
          account={account}
          showName={showName}
        />
      </Body>
      : null
  );
}

const showRules = () => {
  return (
    <RulesWrapper>
      <h3> </h3>
      <div>
             1  1.2%( 1  300 /
          ) .
      </div>
      <div>
         , 5     ,   1.0% ( 1 250 /
          ) .
      </div>
      <div>
          ,     1    .
      </div>
      <div>
              ' ' 
        .
      </div>
      <div>      ' ' .</div>
      <div>
           ,   .
      </div>
      <div>
              1 . 1 
          ,  .
      </div>
      <div>
            5  , 
        (). '  ' 
      </div>
      <div>
            ,   
               
         .
      </div>
    </RulesWrapper>
  );
};

const Body = styled.div`
  box-sizing: border-box;
  height: 100%;
  font-family: Impact, Charcoal, sans-serif;
`;
const StoneContainer = styled.div`
  width: 1100px;
  height: 400px;
  display: flex;
  align-items: center;
  margin: 0 auto;
  border-bottom: 2px solid #303030;
`;
const TradeContainer = styled.div`
width : 1100px;
height: 350px;
margin: 0 auto;
display flex;
`;
const StoneBox = styled.div`
width: 100%;
height: 100%;
overflow:hidden;
position: relative;
display:flex;
justify-content: center;
align-items: center;
`;
const StoneWrapper = styled.div`
  position: absolute;
  width: 90%;
  height: 90%;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  overflow-y: scroll;
  &::-webkit-scrollbar {
    width: 2px;
    background-color: #0f0f0f;
  }
  &::-webkit-scrollbar-thumb {
    background-color: #333333;
  }
`;
const Title = styled.h2`
  margin: 10px auto;
  display: flex;
  align-items: center;
  span {
    margin-right: 10px;
  }
`;
const AddPlaylistBtn = styled.button`
  height: 25px;
  cursor: pointer;
  border-radius: 6px;
  border: 0;
  background-color: #666666;
  color: white;
  &:hover {
    color: black;
  }
`;
const Info = styled.div``;
const Price = styled.div`
  div {
    font-weight: 500;
    display: flex;
  }
`;
const PriceDif = styled.span`
  display: flex;
  color: ${({ color }) => color};
`;
const Composer = styled.div``;
const Lyricist = styled.div``;
const Desc = styled.div`
  margin-top: 20px;
`;
const SellListWrapper = styled.div`
  width: 55%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
`;
const SellNav = styled.div`
  margin: 15px 30px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  span {
    text-align: center;
    width: 100%;
  }
`;
const SellList = styled.li`
  margin: 10px;
  list-style: none;
  overflow: auto;
  li {
    margin: 5px 20px;
  }
`;
const NotifyWrapper = styled.div`
  width: 45%;
`;
const RulesWrapper = styled.div`
  h3 {
    text-align: center;
  }
  div {
    margin: 2px auto;
    font-size: 0.8rem;
  }
`;
