import React, { useState, useEffect, useRef } from "react";
import styled from "styled-components";
import { useSelector, useDispatch } from "react-redux";
import axios from 'axios';
import BuyStoneCard from '../../components/Stones/BuyStoneCard';
import SearchIcon from "@mui/icons-material/Search";
import { useInView } from "react-intersection-observer";
import { loadStoneList, resetStoneList } from '../../actions';

export function BuyStone() {
    /* state
loading:  true  false
loadAll:    20     true    
startIdx:  Idx  20 
keyword:   , search  input   
initial:   (  )
*/
    const [loading, setLoading] = useState(true);
    const [loadAll, setLoadAll] = useState(false);
    const [startIdx, setStartIdx] = useState(1);
    const [keyword, setKeyword] = useState("");
    const [initial, setInitial] = useState(true);

    //scroll
    const [ref, inView] = useInView();

    const account = useSelector((state) => state.accountReducer);
    const stoneList = useSelector((state) => state.buyStoneReducer);
    const dispatch = useDispatch();
    const keywordInput = useRef();

    //  stone 
    const loadStones = () => {
        const server = process.env.REACT_APP_SERVER_ADDRESS || "http://127.0.0.1:12367";
        const req = `${server}/stones/buystone/${account.userId}?startIndex=${startIdx}&endIndex=${startIdx + 19}&keyword=${keyword}`

        setLoading(true);
        axios.get(req)
            .then((res) => {
                const resData = res.data;
                dispatch(loadStoneList(resData))
                if (resData.sellList.length < 20) setLoadAll(true);

                setStartIdx(startIdx + 20);
                setLoading(false);
            }
            )
    }
    const showStones = () => {
        return (stoneList.stones.map((stone, idx) => {
            return <BuyStoneCard stone={stone} />
        }))
    }

    const handleInputEnter = (e) => {
        if (e.key == 'Enter') {
            search();
        }
    }

    const search = () => {
        setKeyword(keywordInput.current.value);
    }

    useEffect(() => {
        //          load
        if (inView && !loading && !loadAll && startIdx !== 1) {
            loadStones();
        }
    }, [inView, loading]);

    useEffect(() => {
        //   list
        dispatch(resetStoneList());

        //   idx   load
        if (startIdx !== 1) {
            setStartIdx(1);
        } else {
            loadStones();
            if (initial) {
                setInitial(false);
            }
        }
    }, [keyword])

    useEffect(() => {
        //  loadAll true    
        if (startIdx === 1 && loadAll) {
            setLoadAll(false);
            loadStones();
        } else if (startIdx === 1 && !initial) {
            loadStones();
        }
    }, [startIdx])

    return (
        <Body>
            <StoneContainer>
                <Nav>
                    <Title><h1>Collect Your Stones</h1></Title>
                    {/*   
                    <Notify>*      . </Notify>
                    */}
                    <Notify>{account.isConnect ? ' ' : '*  stone    '}</Notify>
                    <SearchWrap>
                        <SearchInput ref={keywordInput} type="text" placeholder="    ." onKeyPress={handleInputEnter} />
                        <SearchButton onClick={search}>
                            <SearchIcon />
                        </SearchButton>
                    </SearchWrap>
                </Nav>
                <StonesWrapper>
                    {showStones()}
                </StonesWrapper>
            </StoneContainer>
            <div id="observer" ref={ref}> </div>
        </Body>
    );
}

// styled
const Body = styled.div`
height: 100%;
margin-top: 2rem;
`;

const StoneContainer = styled.div`
display:flex;
flex-direction: column;
margin: 0 auto;
`;
const Nav = styled.div`
display: flex;
flex-direction: column;
width: 980px;
margin: 0 auto;
`;
const Title = styled.div`
margin: 0 auto;
`;
const Notify = styled.span`
height: 20px;
font-size: 0.8rem;
white-space: pre;
`;
const SearchWrap = styled.div`
position: relative;
display: flex;
justify-content: flex-end;
`;

const SearchInput = styled.input`
width: 300px;
border: 3px solid #848cb5;
border-right: none;
padding: 5px;
height: 20px;
border-radius: 10px 0 0 10px;
outline: none;
color: #6b6b84;
&:focus {
color: #3b3b54;
}
`;
const SearchButton = styled.button`
  width: 40px;
  height: 36px;
  border: 1px solid #848cb5;
  background: #848cb5;
  text-align: center;
  color: #fff;
  border-radius: 0 10px 10px 0;
  cursor: pointer;
  font-size: 20px;
`;
const StonesWrapper = styled.div`
width: 980px;
margin: 0 auto;
display: flex;
flex-wrap: wrap;
justify-content: space-around;
`;
